import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import FacilityCon from "./FacilityCon.js";

const Facility = () => {
  // useLocation Hook   
  const location = useLocation();

  useEffect(() => {
    console.log(location);
  }, [location]);

  //  hash  
  useEffect(() => {
    const myHash = location.hash;
    console.log(myHash);
    //  hash  hash === id   
    //  hash  "#restaurant"  
    if (myHash) {
      const bb = document.querySelector(myHash);
      console.log(bb);
      bb.scrollIntoView({ behavior: "smooth" });
    } else {
      const first = document.querySelector(".faclity_content");
      first.scrollIntoView({ behavior: "smooth" });
    }

    // if (myHash === undefined) {
    //   first.scrollIntoView({ behavior: "smooth" });
    // } else {
    //   const bb = document.querySelector(myHash);
    //   console.log(bb);
    //   bb.scrollIntoView({ behavior: "smooth" });
    // }
  }, [location]);

  const bg = {
    width: "100%",
    padding: "125px 0",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  };

  const bgH = {
    textAlign: "center",
    color: "#fff",
  };

  const facility = {
    width: "90%",
    margin: "0 auto",
    padding: "90px 0",
    textAlign: "center",
  };

  const facilityInfo = [
    {
      id: 1,
      value: "restaurant",
      name: "",
      content:
        "OPSOYE         .    VIP .",
      txt: "JEJU FOOD",
    },
    {
      id: 2,
      value: "kidsroom",
      name: "",
      content:
        "          .",
      txt: "JEJU KIDS",
    },
    {
      id: 3,
      value: "giftshop",
      name: "",
      content:
        "  , OPSOYE        .",
      txt: "JEJU SHOP",
    },
    {
      id: 4,
      value: "fitness",
      name: "",
      content:
        "        . OPSOYE  .",
      txt: "JEJU SPORTS",
    },
    {
      id: 5,
      value: "pool",
      name: "",
      content:
        "                   . ",
      txt: "JEJU POOL",
    },
  ];

  return (
    <div className="facility">
      <div style={bg} className="bg">
        <h1 style={bgH}> </h1>
      </div>
      <div className="faclity_content" style={facility}>
        <div className="title">
          <h1> </h1>
          <span>
            OPSOYE        
               .      
                   .  
                   
            .
          </span>
        </div>
        {facilityInfo.map((itm, key) => (
          <FacilityCon
            id={itm.value}
            key={key}
            title={itm.name}
            txt={itm.content}
            img={itm.value}
            eng={itm.txt}
          />
        ))}
      </div>
    </div>
  );
};

export default Facility;
