import React, { useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { EffectFade, Autoplay, Pagination, Navigation } from "swiper";
import "swiper/css/effect-fade";
// Import Swiper styles
import "swiper/css";
import "swiper/css/autoplay";
import AOS from "aos";
import "aos/dist/aos.css";

const Opsoyefood = () => {
  useEffect(() => {
    AOS.init();
  });

  const bg = {
    width: "100%",
    padding: "125px 0",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  };

  const bgH = {
    textAlign: "center",
    color: "#fff",
  };
  return (
    <div className="opsoyefood">
      <div style={bg} className="bg">
        <h1 style={bgH}>OPSOYE </h1>
      </div>
      <div className="opsoyefood_content">
        <div className="food_top_con">
          <h1>OPSOYE FOOD</h1>
          <span>
                     
                     
              .
          </span>
          <img src={require("../images/food/top_img.jpg")} />
        </div>
        <div className="food_farm">
          <div className="title">
            <p>OPSOYE FARM</p>
            <h1>   </h1>
            <span>
                      .
            </span>
          </div>
          <div className="farm_con">
            <div>
              <img src={require("../images/food/food01.jpg")} />
              <h1>(6)</h1>
              <span>   10  </span>
            </div>
            <div>
              <img src={require("../images/food/food02.jpg")} />
              <h1>(7)</h1>
              <span>     </span>
            </div>
            <div>
              <img src={require("../images/food/food03.jpg")} />
              <h1>  (7)</h1>
              <span>      </span>
            </div>
            <div>
              <img src={require("../images/food/food04.jpg")} />
              <h1>(6)</h1>
              <span>    </span>
            </div>
          </div>
          <div className="farm_mo">
            <Swiper
              className="opsoyefood"
              pagination={{
                clickable: true,
                parallax: true,
                watchSlidesProgress: true,
                type: "fraction",

                el: ".swiper_pagination",
                formatFractionCurrent: function (number) {
                  return "0" + number;
                },
                formatFractionTotal: function (number) {
                  return "0" + number;
                },
              }}
              navigation={{
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
              }}
              modules={[EffectFade, Autoplay, Pagination, Navigation]}
              spaceBetween={50}
              slidesPerView={1.5}
              // onSlideChange={() => console.log('slide change')}
              // onSwiper={(swiper) => console.log(swiper)}
            >
              <SwiperSlide>
                <div>
                  <img src={require("../images/food/food01.jpg")} />
                  <h1>(6)</h1>
                  <span>   10  </span>
                </div>
              </SwiperSlide>
              <SwiperSlide>
                <div>
                  <img src={require("../images/food/food02.jpg")} />
                  <h1>(7)</h1>
                  <span>     </span>
                </div>
              </SwiperSlide>
              <SwiperSlide>
                <div>
                  <img src={require("../images/food/food03.jpg")} />
                  <h1>  (7)</h1>
                  <span>
                          
                  </span>
                </div>
              </SwiperSlide>
              <SwiperSlide>
                <div>
                  <img src={require("../images/food/food04.jpg")} />
                  <h1>(6)</h1>
                  <span>    </span>
                </div>
              </SwiperSlide>
            </Swiper>
          </div>
        </div>

        <div className="food_farm foods">
          <div
            className="bg_color"
            data-aos="fade-right"
            data-aos-duration="1500"
          ></div>
          <div className="flex">
            <div className="title">
              <p>OPSOYE FARM</p>
              <h1>   </h1>
              <span>OPSOYE    .</span>
            </div>

            <div className="farm_mo">
              <Swiper
                className="opsoyefood"
                pagination={{
                  clickable: true,
                  parallax: true,
                  watchSlidesProgress: true,
                  type: "fraction",

                  el: ".swiper_pagination",
                  formatFractionCurrent: function (number) {
                    return "0" + number;
                  },
                  formatFractionTotal: function (number) {
                    return "0" + number;
                  },
                }}
                navigation={{
                  nextEl: ".swiper-button-next",
                  prevEl: ".swiper-button-prev",
                }}
                modules={[EffectFade, Autoplay, Pagination, Navigation]}
                spaceBetween={50}
                slidesPerView={1.5}
                // onSlideChange={() => console.log('slide change')}
                // onSwiper={(swiper) => console.log(swiper)}
              >
                <SwiperSlide>
                  <div>
                    <img src={require("../images/food/cake.jpg")} />
                    <h1></h1>
                    <span>    .</span>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div>
                    <img src={require("../images/food/burger.jpg")} />
                    <h1></h1>
                    <span>    .</span>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div>
                    <img src={require("../images/food/taco.jpg")} />
                    <h1>JEJU's </h1>
                    <span>     .</span>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div>
                    <img src={require("../images/food/steak.jpg")} />
                    <h1> </h1>
                    <span>1   .</span>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div>
                    <img src={require("../images/food/meet.jpg")} />
                    <h1>  </h1>
                    <span>     .</span>
                  </div>
                </SwiperSlide>
              </Swiper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Opsoyefood;
